











	import Vue from 'vue';
    import {events} from "../../lib/Utils";

	export default Vue.extend({
		data(){
		    return {
		        callSuccess: false
		    };
		},

		props: ['tel', 'color'],

        methods: {
            triggerCall(){
                const result = events.emit('trigger-call', this.tel);

                if(result === null){
	                this.callSuccess = true;
                }
            }
        }
	});
